.scroll-wrapper {
  height: 667px;
  overflow: hidden;
  position: relative;
}
.pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all;
}
.pulldown-wrapper .after-trigger {
  margin-top: 10px;
}
.pulldown-wrapper .loading {
  text-align: center;
}
